import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQueryCafe } from 'ionic-vhframeworks';
import { ExpenseA4Component } from 'src/app/cafe/components/print/expenses/expense-a4/expense-a4.component';
import { ExpensesA5Component } from 'src/app/cafe/components/print/expenses/expenses-a5/expenses-a5.component';
import { ExpenseK80Col4Component } from 'src/app/cafe/components/print/expenses/expense-k80-col4/expense-k80-col4.component';
import { ExpenseK80Col3Component } from 'src/app/cafe/components/print/expenses/expense-k80-col3/expense-k80-col3.component';
import { ExpenseK57Col3Component } from 'src/app/cafe/components/print/expenses/expense-k57-col3/expense-k57-col3.component';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { FunctionService } from 'src/app/cafe/services/function.service';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {
  @ViewChild('nameGroup') nameGroup; // biến lấy tên nhóm (form kg cho dùng ngmodel)
  dataRestore: any = this.router.getCurrentNavigation().extras.state;
  expendGroup: any = this.vhQueryCafe.getlocalSpending_groups() // nhóm chi
  data: any;
  check: boolean = false; // biến bắt load dữ liệu khi xóa hoặc cập nhật phiếu chi
  type_expense: any;
  radioValue: any;
  detailExpensesForm: FormGroup;
  money_currency: any;
  list_suppliers: any = [];
  listPaymentBank: any = [];
  disabled = true;
  isDisabled: boolean = false; //biến cờ true khi phiếu ở trạng thái đã hủy
  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    private vhQueryCafe: VhQueryCafe,
    private vhComponent: VhComponent,
    public languageService: LanguageService,
    private vhAuth: VhAuth,
    private fncService:FunctionService
  ) {
    this.data = this.router.getCurrentNavigation().extras.state.data;
    this.type_expense = this.data.bill_type;
    if (this.type_expense == 26 || this.type_expense == 28) {
      this.isDisabled = true;
    }
    this.detailExpensesForm = new FormGroup({
      date: new FormControl(this.data.date),
      customer: new FormControl({ value: this.data.bill_type == 12 || this.data.bill_type == 28 ? this.data.id_supplier : this.data.name, disabled: this.isDisabled }, Validators.compose([Validators.required])),
      total: new FormControl({ value: this.data.total, disabled: this.isDisabled }, Validators.compose([Validators.required, Validators.pattern("(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)")])),
      content: new FormControl({ value: this.data.content, disabled: this.isDisabled }),
      payment_type: new FormControl(this.data.payment_type),
      type_receipt: new FormControl(this.data.bill_type),
      id_branch: new FormControl(this.data.id_branch),
      id_group: new FormControl({value:this.data.bill_type == 8 || this.data.bill_type == 26 ? this.data.id_group : null,disabled: this.isDisabled })
    });
    this.radioValue = this.data.payment_type.toString();
  }
  ngOnInit(): void {
    this.list_suppliers = this.vhQueryCafe.getlocalSuppliers();
  }

  ngAfterViewInit() {
    this.vhAlgorithm.waitingStack().then(() => {
      this.money_currency = this.vhAlgorithm.vhnumeral(".price");
    });
  }
  /**
   * check validate hợp lệ
   * @param $event date
   */
   changeDate(event) {
    this.vhQueryCafe.getValidityDate(event)
      .then((validitydate: any) => {
        if (validitydate) {
          this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 8000)
          this.detailExpensesForm.controls['date'].setValue(new Date())
        }
        else {
          this.updateBill({date : this.detailExpensesForm.value.date})
          this.data.date = event
        }
      })

  }

  /**back */
  goBack() {
    let data = { ...this.dataRestore.dataRestore }
    data.check = true
    this.router.navigate(['/cafe/dashboard/management/cashflow/expenses'], {
      state: this.check ? data : this.dataRestore.dataRestore
    });
  }


  /**Zorro modal */
  isVisible = false;
  showModal(): void {
    this.isVisible = true;
  }
  handleOk(): void {
    this.isVisible = false;
    if (this.vhAuth.checkMyPermission("cashflow_cancel_expenses")) this.deleteExpenses();
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  handleCancel(): void {
    this.isVisible = false;
  }

  /**Xóa phiếu chi */
  deleteExpenses() {
    this.vhComponent.showLoading("").then(() => {
      this.vhQueryCafe
        .deleteBill(this.data['_id'])
        .then((res: any) => {
          this.vhComponent.hideLoading(0);
          this.check = true
          this.vhComponent.showToast(1000,
            (this.languageService.translate("Expenses has been canceled successfully")), "success-toast"
          );
          setTimeout(() => {
            this.goBack();
          }, 200);
        }, err => { })
    })
  }

  /**Update price */
  updateTotal() {
    if (this.money_currency.getRawValue() != "")
      this.updateBill({ total: parseInt(this.money_currency.getRawValue()), payment: parseInt(this.money_currency.getRawValue()) })

  }

  /**Update bill */
  updateBill(data) {
    this.vhQueryCafe.getValidityDate(event)
    .then((validitydate: any) => {
      if (validitydate) {
        this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 8000)
      }
      else {
        if (this.type_expense == 12) data['id_supplier'] = data['customer']
        else { data['name'] = data['customer'] }
        this.check = true
        data['payment_type'] = this.data.payment_type;
        data['total'] = parseInt(this.money_currency.getRawValue());
        data['payment'] = parseInt(this.money_currency.getRawValue());
        if (this.detailExpensesForm.valid) {
          this.vhQueryCafe.updateBill(this.data['_id'], data)
            .then(res => {
            })
            .catch(err => console.error('update failed => ' + err))
        }
      }
    })
  }
  showPrint() {
    this.checkPrint().then((printer) => {
      if (printer) {
        this.data['content'] = this.detailExpensesForm['value'].content
        this.data['supplier'] = [12, 28].includes(this.type_expense) ? this.vhQueryCafe.getlocalSupplier(this.data['id_supplier']) : this.detailExpensesForm['value'].customer
        this.data['employee'] = this.vhAuth.getlocalEmployee(this.data['id_employee'])
        this.data['total'] = parseInt(this.money_currency.getRawValue())
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), {
          billInfo: this.data,
          printer: printer,
        }, false, false, `modal-print-${printer['_id']}`)
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => { })
          })
      }
    }, (err) => {
      let name :any= 'Pay for liabilities / others'
      this.vhComponent.alertMessage(this.languageService.translate("Function"), "", this.languageService.translate("Please turn on the printer function"), "OK")
        .then(() => this.router.navigate(['/cafe/dashboard/printer','page_desktop_expense'],{state : name}))
    })

  }
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return ExpenseK57Col3Component
      case 'print_size_k80_3c': return ExpenseK80Col3Component
      case 'print_size_k80_4c': return ExpenseK80Col4Component
      case 'print_size_a5': return ExpensesA5Component
      case 'print_size_a4': return ExpenseA4Component
      default: return ExpenseA4Component
    }
  }
  /*----------Print------------------*/
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQueryCafe.getLocalPrintPage('page_desktop_expense')
      if (printer  && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }

  /***********Thêm xóa sủa nhóm chi********** */
  isVisiblelist: boolean = false; // popup danh sách nhóm
  activeButton: boolean = false
  id_group_default: any = this.expendGroup.length ? this.expendGroup.filter(item => item.default == true) : []
  defaultGroup = this.id_group_default.length ? this.id_group_default[0]._id : null
  addGroup(nameGroup): void {
    let index = this.expendGroup.findIndex(item => item.default)
    this.activeButton = true
    let value = {
      name: nameGroup.value,
      default: index != -1 ? false : true
    }
    if (value.name != "") {
      this.vhQueryCafe.addSpending_group(value).then((res) => {
        this.expendGroup = [...this.expendGroup, res];
        this.nameGroup.nativeElement.value = ' ';
        this.activeButton = false
      })
    } else this.vhComponent.alertMessageDesktop("warning", "Chưa nhập tên nhóm")
  }
  setDefault(item) {
    let index = this.expendGroup.findIndex(item => item.default)
    item.default = true
    if (index != -1) {
      this.expendGroup[index].default = false;
      Promise.all([this.vhQueryCafe.updateSpending_group(item._id, item), this.vhQueryCafe.updateSpending_group(this.expendGroup[index]._id, this.expendGroup[index])])
        .then(() => { })
    } else {
      this.vhQueryCafe.updateSpending_group(item._id, item).then(() => { })
    }
  }
  openLisGroup() {
    this.id_group_default = this.expendGroup.length ? this.expendGroup.filter(item => item.default == true) : []
    this.defaultGroup = this.id_group_default.length ? this.id_group_default[0]._id : null
    this.isVisiblelist = true
  }
  handleCancellist() {
    this.isVisiblelist = false;
  }

  changeEdit: any = ''
  editGroup(_id) {
    this.changeEdit = _id
  }
  saveEditGroup(item) {
    let value = {
      name: item.name
    }
    this.vhQueryCafe.updateSpending_group(item._id, value).then(() => {
      this.changeEdit = ""
    })
  }


  deleteGroup(_id, index) {
    this.vhQueryCafe.deleteSpending_group(_id).then((bool) => {
      if (bool) this.expendGroup.splice(index, 1)
      else this.vhComponent.alertMessageDesktop("error", "Không xóa được nhóm này")
    })
  }
}
