import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NavController } from '@ionic/angular';
import { VhAlgorithm, VhAuth, VhQueryCafe } from 'ionic-vhframeworks';
import { ExpenseA4Component } from 'src/app/cafe/components/print/expenses/expense-a4/expense-a4.component';
import { ExpensesA5Component } from 'src/app/cafe/components/print/expenses/expenses-a5/expenses-a5.component';
import { ExpenseK80Col4Component } from 'src/app/cafe/components/print/expenses/expense-k80-col4/expense-k80-col4.component';
import { ExpenseK80Col3Component } from 'src/app/cafe/components/print/expenses/expense-k80-col3/expense-k80-col3.component';
import { ExpenseK57Col3Component } from 'src/app/cafe/components/print/expenses/expense-k57-col3/expense-k57-col3.component';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { FunctionService } from 'src/app/cafe/services/function.service';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {
  @ViewChild('nameGroup') nameGroup; // biến lấy tên nhóm (form kg cho dùng ngmodel)
  dataRestore: any = this.router.getCurrentNavigation().extras.state;
  listPaymentBank: any = [];
  list_suppliers: any = [];
  money_currency: any;
  radioValue: any = 'A';
  addExpensesForm: FormGroup;
  type: any = '12'; // Biến lưu loại phiếu chi được chọn -> 12: Phiếu chi công nợ nhà cung cấp
  nzOptions: any;
  billInfo: any = { employee: this.vhAuth.getUser() };
  expendGroup: any = this.vhQueryCafe.getlocalSpending_groups() // nhóm chi
  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    private vhQueryCafe: VhQueryCafe,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    public languageService: LanguageService,
    private navCtrl: NavController,
    private fncService: FunctionService
  ) {

    this.getPaymentBankList();
    let options = [
      {
        value: '1',
        label: this.languageService.translate("Cash"),
        isLeaf: true
      },
      {
        value: '3',
        label: this.languageService.translate("Account"),
        children: this.listPaymentBank
      }
    ]
    this.nzOptions = options;
  }

  ngOnInit(): void {
    this.list_suppliers = this.vhQueryCafe.getlocalSuppliers();
    this.initForm();

    let printer = this.vhQueryCafe.getLocalPrintPage('page_desktop_expense')

    if (printer) this.checkPrinter = printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable;
  }
  checkPrinter: boolean = false;
  ngAfterViewInit() {
    this.vhAlgorithm.waitingStack().then(() => {
      this.money_currency = this.vhAlgorithm.vhnumeral(".price");
    });
  }
  /**
   * khởi tạo form có các trường được required trong bill
   * @example initForm()
   */
  initForm() {
    let id_group_default: any = this.expendGroup.length ? this.expendGroup.filter(item => item.default == true) : []
    let required: any = this.type == 8 ? Validators.compose([Validators.required]) : ""
    this.addExpensesForm = new FormGroup({
      date: new FormControl(new Date().toISOString()),
      customer: new FormControl("", Validators.compose([Validators.required])),
      total: new FormControl("", Validators.compose([Validators.required, Validators.pattern("(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)")])),
      content: new FormControl(""),
      payment_type: new FormControl('1'),
      wallet: new FormControl(null),
      type_receipt: new FormControl(this.type),
      id_group: new FormControl(id_group_default.length ? id_group_default[0]._id : null, required)
    });
  }

  /**
  * check validate hợp lệ
  * @param $event date
  */
  changeDate(event) {
    this.vhQueryCafe.getValidityDate(event)
      .then((validitydate: any) => {
        if (validitydate) {
          this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 8000)
          this.addExpensesForm.controls['date'].setValue(new Date())
        }
      })
  }

  /**Get payment bank list */
  getPaymentBankList() {
    let listPayment = this.vhQueryCafe.getlocalWallets().filter(item => item._id != 'id_cash' && item.id_branch == this.vhQueryCafe.getDefaultBranch()._id);
    this.listPaymentBank = listPayment.map(item => {
      return { value: item._id, label: item.name, isLeaf: true }
    })
  }

  /**Hàm chọn loại phiếu chi*/
  chooseType(value) {
    this.initForm();
  }

  /**Khởi tạo bill*/
  exportBill(formValue) {
    const bill: any = {
      bill_code: this.vhAlgorithm.createBillCode(new Date(formValue["date"])),
      date: new Date(formValue["date"]),
      id_employee: this.billInfo['employee']._id,
      id_branch: this.vhQueryCafe.getDefaultBranch()._id,
      bill_type: parseInt(this.type),
      content: formValue["content"],
      payment_type: formValue["payment_type"],
      tax: 0,
      total: parseFloat(this.money_currency.getRawValue()),
      payment: parseFloat(this.money_currency.getRawValue())
    }
    if (bill['payment_type'][0] == '3') {
      bill['id_wallet'] = bill['payment_type'][1];
      bill['payment_type'] = 3;
    } else {
      bill['payment_type'] = 1;
      bill['id_wallet'] = 'id_cash';
    }
    if (this.type == 12) {
      bill['id_supplier'] = formValue['customer'],
        this.billInfo['supplier'] = this.vhQueryCafe.getlocalSupplier(formValue['customer'])
    }
    else {
      bill.name = formValue["customer"]
      bill.id_group = formValue["id_group"]
    }
    return bill;
  }

  /**Hàm tạo phiếu chi */
  addExpenses(print) {
    this.vhComponent.showLoading("").then(() => {
      const bill = this.exportBill(this.addExpensesForm['value'])
      console.log('createBill', bill); 
      this.vhQueryCafe
        .createBill(bill)
        .then((res: any) => {
          this.billInfo = { ...this.billInfo, ...bill, id: res._id }
          this.vhComponent.showToast(1500, (this.languageService.translate("Expenses has been created successfully")), "success-toast");
          this.vhComponent.hideLoading(0).then(() => {
            if (print) this.showPrint();
            else setTimeout(() => this.goBack(true), 200)
          })
        }, err => { })
    })
  }

  /**back */
  goBack(value?) {
    let data = { ...this.dataRestore }
    data.check = true
    this.router.navigate(['/cafe/dashboard/management/cashflow/expenses'], {
      state: value ? data : this.dataRestore
    });
  }

  /**Zorro modal save*/
  isPrint: boolean
  isVisible = false;
  showModal(value): void {
    this.isVisible = true;
    this.isPrint = value
  }
  handleOk(): void {
    this.isVisible = false;
    this.addExpenses(this.isPrint);
  }
  handleCancel(): void {
    this.isVisible = false;
  }

  /** kiểm tra dữ liệu máy in và show component in tương ứng
  * @example this.showPrint() 
  */
  showPrint() {
    this.checkPrint().then((printer) => {
      if (printer) {
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), { billInfo: this.billInfo, printer: printer }, false, false, `modal-print-${printer['_id']}`)
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => { this.goBack(true) })
          })
      }
    }, err => { this.goBack(true) })
  }
  /**
   * trả về component của trang in tương ứng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return ExpenseK57Col3Component
      case 'print_size_k80_3c': return ExpenseK80Col3Component
      case 'print_size_k80_4c': return ExpenseK80Col4Component
      case 'print_size_a5': return ExpensesA5Component
      case 'print_size_a4': return ExpenseA4Component
      default: return ExpenseA4Component
    }
  }
  /**
     * lấy thông tin dữ liệu trang in
     * @returns Promise<any>
     * @example this.checkPrint()
     */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQueryCafe.getLocalPrintPage('page_desktop_expense')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }


  /***********Thêm xóa sủa nhóm chi********** */
  isVisiblelist: boolean = false; // popup danh sách nhóm
  activeButton: boolean = false;
  /**
   * tạo nhóm chi lên DB
   * @param nameGroup : tên nhóm chi khác
   * @example this.addGroup('Tiền mua nội thất')
   */
  addGroup(nameGroup): void {
    this.activeButton = true
    let index = this.expendGroup.findIndex(item => item.default)
    let value = {
      name: nameGroup.value,
      default: index != -1 ? false : true
    }
    if (value.name != "") {
      this.vhQueryCafe.addSpending_group(value).then((res) => {
        this.expendGroup = [...this.expendGroup, res];
        this.nameGroup.nativeElement.value = ' ';
        this.activeButton = false
      })
    } else this.vhComponent.alertMessageDesktop("warning", "Chưa nhập tên nhóm")
  }

  /**
   * Cập nhật trường default của nhóm chi
   * @param item : nhóm chi đã chọn
   * @example this.setDefault(spending_group)
   */
  setDefault(item) {
    let index = this.expendGroup.findIndex(item => item.default)
    item.default = true
    if (index != -1) {
      this.expendGroup[index].default = false;
      Promise.all([this.vhQueryCafe.updateSpending_group(item._id, item), this.vhQueryCafe.updateSpending_group(this.expendGroup[index]._id, this.expendGroup[index])])
        .then(() => { })
    } else {
      this.vhQueryCafe.updateSpending_group(item._id, item).then(() => { })
    }
  }

  openLisGroup() {
    let id = this.expendGroup.length ? this.expendGroup.filter(item => item.default == true) : []
    this.isVisiblelist = true
    this.addExpensesForm['value'].id_group = id.length ? id[0]._id : null
  }

  handleCancellist() {
    this.isVisiblelist = false;
  }

  changeEdit: any = ''
  editGroup(_id) {
    this.changeEdit = _id
  }
  saveEditGroup(item) {
    let value = {
      name: item.name
    }
    this.vhQueryCafe.updateSpending_group(item._id, value).then(() => {
      this.changeEdit = ""
    })
  }

  deleteGroup(_id, index) {
    this.vhQueryCafe.deleteSpending_group(_id).then((bool) => {
      if (bool) {
        this.expendGroup.splice(index, 1)
        this.initForm()
      }
      else this.vhComponent.alertMessageDesktop("error", "Không xóa được nhóm này")
    })
  }

}
